<script>
  import FaceButton from './FaceButton.svelte';

  export let examples = [];

  let selectedIndex = 0;
  let exampleTipPosition = '50%';
  const faceButtonGap = 4.1;

  function selectExample(index) {
    selectedIndex = index;

    updateTipPosition();
  }

  function updateTipPosition() {
    let baseOffset = ((faceButtonGap * (examples.length - 1)) / 2) * -1;
    let offset = baseOffset + selectedIndex * faceButtonGap;
    exampleTipPosition = `calc(50% + ${offset}rem)`;
  }

  $: {
    selectedIndex = 0;
    updateTipPosition();
  }
</script>

<style type="text/scss">.example {
  font-family: 'Overlock', cursive;
  font-size: 1.2rem;
  position: relative;
  background: #fff;
  border: 1px solid #ccc;
  padding: 0.5rem;
  border-radius: 0.5rem;
  min-height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center; }
  .example:after, .example:before {
    top: 100%;
    left: var(--tip-potition);
    border: solid transparent;
    content: ' ';
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none; }
  .example:after {
    border-color: transparent;
    border-top-color: #fff;
    border-width: 1rem;
    margin-left: -1rem; }
  .example:before {
    border-color: transparent;
    border-top-color: #ccc;
    border-width: calc(1rem + 1px);
    margin-left: calc(-1rem - 1px); }

.button-container {
  display: flex;
  grid-gap: 1rem;
  justify-content: center; }
</style>

<div>
  <p class="example" style="--tip-potition: {exampleTipPosition}">{examples[selectedIndex]}</p>
  <div class="button-container">
    {#each examples as e, i}
      <FaceButton selected={selectedIndex === i} type={`type${i + 1}`} on:click={() => selectExample(i)} />
    {/each}
  </div>
</div>
